div {
    list-style-type: none;
}
.footer {
    background-color: #191f28;
    .container {
        max-width: 1440px;
        width: 100%;
        margin: 0 auto;
        .inner {
            display: flex;
            justify-content: space-between;
            align-items: center;
            height: 180px;
            padding: 0 48px;
            .name {
                font-size: 14px;
                font-weight: 400;
                color: #646f7c;
            }
            .sns_list {
                font-size: 0;
                .list_item {
                    display: inline-block;
                    .icon_github {
                        opacity: 0.4;
                        display: block;
                        width: 46px;
                        height: 46px;
                        background-image: url("../images/GitHub-logo.png");
                        background-repeat: no-repeat;
                        background-size: 46px auto;
                        background-position: center center;
                        font-size: 0;
                        &:hover {
                            opacity: 0.8;
                        }
                    }
                }
            }
        }
    }
}
@media screen and (max-width: 768px) {
    .footer {
        .container {
            .inner {
                height: 100px;
                padding: 0 28px;
            }
        }
    }
}
