.projectItem {
    width: 48.5%;
    margin-bottom: 80px;
    &:nth-last-child(-n + 2) {
        margin-bottom: 0;
    }
    .wrapper {
        display: block;
        &:hover {
            transform: translateY(-10px);
        }
        transition: 0.27s ease;
        .img_box {
            overflow: hidden;
            position: relative;
            width: 100%;
            height: 100%;
            padding-top: 56.14%;
            background-size: auto 100%;
            background-position: center center;
            border-radius: 20px;
        }
        .info_box {
            margin-top: 20px;
            .title {
                font-size: 20px;
                font-weight: 500;
                color: #191f28;
            }
            .desc {
                margin-top: 2px;
                font-size: 16px;
                font-weight: 300;
                color: #b0b8c1;
            }
        }
    }
}
@media screen and (max-width: 768px) {
    .projectItem {
        width: 100%;
        margin-bottom: 32px;
        &:nth-last-child(-n + 2) {
            margin-bottom: 32px;
        }
        &:last-child {
            margin-bottom: 0;
        }
        .wrapper {
            .img_box {
                border-radius: 9px;
            }
            .info_box {
                margin-top: 8px;
                .title {
                    font-size: 16px;
                    line-height: 1.3;
                }
                .desc {
                    font-size: 12px;
                }
            }
        }
    }
}
