$grey: #374553;
$light-grey: #646f7c;
$bb: 1px solid rgba(0, 27, 55, 0.1);

html,
body,
#root,
#root > div {
    position: relative;
    height: 100%;
}

#root {
    font-family: "Pretendard", "sans-serif";
}

.container {
    max-width: 1600px;
    width: 100%;
    margin: 0 auto;

    .inner {
        padding: 0 48px;
    }
}

.loading_img {
    height: 100vh;
    background-image: url("../images/Loading_icon.gif");
    background-position: top 20vh center;
    background-size: 250px auto;
    background-repeat: no-repeat;
}

.projects_container {
    margin-top: 110px;
    .projects_list {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
    }
}

.contents {
    padding-bottom: 120px;
}

.main_visual {
    padding-top: 120px;
    .text_container {
        padding: 78px 40% 20px 0;
        // background-image: url("../images/avatar.png");
        // background-repeat: no-repeat;
        // background-position: right 15% bottom 5%;
        // background-size: 250px auto;
        .code_text {
            display: inline-block;
            font-size: 58px;
            font-weight: 800;
            background: rgb(71, 191, 216);
            background: linear-gradient(
                90deg,
                rgba(71, 191, 216, 1) 0%,
                rgba(141, 122, 230, 1) 100%
            );
            -webkit-background-clip: text;
            -webkit-text-fill-color: transparent;
            .skill_list {
                font-size: 0;
                .item {
                    &:last-child {
                        margin-right: 0;
                    }
                    display: inline-block;
                    font-size: 13px;
                    font-weight: 600;
                    margin-right: 8px;
                    vertical-align: top;
                }
            }
        }
        .title {
            font-size: 24px;
            color: $grey;
            font-weight: 700;
            margin-top: 44px;
        }
        .para {
            font-size: 18px;
            font-weight: 300;
            line-height: 26px;
            letter-spacing: 1.2px;
            margin-top: 6px;
            color: $light-grey;
        }
    }
}

.sub_visual {
    padding-top: 120px;
    max-width: 720px;
    width: 100%;
    margin: 0 auto;
    .rough_info {
        padding-top: 80px;
        .title {
            font-size: 24px;
            font-weight: 500;
            color: $grey;
        }
        .sort {
            margin-top: 16px;
            .badge {
                display: inline-block;
                padding: 2px 10px;
                border-radius: 12px;
                font-size: 14px;
                font-weight: 300;
                color: #fff;
                background-color: #2c82ff;
            }
        }
        .img_container {
            margin-top: 56px;
            padding-top: 56.14%;
            background-size: cover;
            background-position: center center;
            background-repeat: no-repeat;
            border-radius: 20px;
            // border: 1px solid rgba(0, 27, 55, 0.1);
        }
    }
    .detail_info {
        margin-top: 40px;
        .info_list {
            .item {
                display: flex;
                padding: 20px 0;
                border-bottom: $bb;
                &:last-child {
                    border-bottom: none;
                }
                .tit {
                    min-width: 60px;
                    font-size: 14px;
                    font-weight: 300;
                    color: $light-grey;
                }
                .desc {
                    font-size: 14px;
                    font-weight: 400;
                    color: $grey;
                }
            }
        }
    }
}

.about_main {
    padding: 80px 0 80px 0;
    margin-bottom: 80px;
    border-bottom: $bb;
    display: table;
    width: 100%;
    table-layout: fixed;
    .img_container {
        width: 150px;
        display: table-cell;
        vertical-align: middle;
        .img_wrapper {
            overflow: hidden;
            border-radius: 14px;
            img {
                display: block;
                width: 100%;
            }
        }
    }
    .text_container {
        display: table-cell;
        padding-left: 60px;
        vertical-align: middle;
        .title {
            font-size: 32px;
            font-weight: 500;
            line-height: 1.3;
            color: #364553;
        }
        .para {
            margin-top: 16px;
            font-size: 20px;
            font-weight: 400;
            color: $light-grey;
        }
    }
}

.history {
    display: table;
    width: 100%;
    table-layout: fixed;
    & + & {
        margin-top: 150px;
    }
    .subject {
        display: table-cell;
        width: 210px;
        font-size: 24px;
        font-weight: 500;
        color: $grey;
    }
    .list_wrap {
        display: table-cell;
        vertical-align: top;
        padding-left: 100px;
        .work_list {
            display: block;
            .item {
                margin-bottom: 42px;
                &:last-child {
                    margin-bottom: 0;
                }
                .title {
                    font-size: 20px;
                    font-weight: 400;
                    line-height: 1.25;
                    color: $grey;
                }
                .period {
                    display: block;
                    margin-top: 10px;
                    font-size: 16px;
                    font-weight: 300;
                    color: #757e85;
                }
                .para {
                    margin-top: 10px;
                    font-size: 16px;
                    font-weight: 300;
                    color: #36444b;
                }
            }
        }
    }
}

.coming_soon {
    height: 100vh;
    .para {
        display: flex;
        justify-content: center;
        align-items: center;
        height: 100%;
        font-size: 38px;
        font-weight: 500;
    }
}

@media screen and (max-width: 768px) {
    .container {
        .inner {
            padding: 0 16px;
        }
    }

    .contents {
        padding-bottom: 64px;
    }
    .main_visual {
        padding-top: 64px;
        .text_container {
            padding: 20px 0;
            .code_text {
                font-size: 28px;
                .skill_list {
                    .item {
                        font-size: 12px;
                        margin-right: 3px;
                    }
                }
            }
            .title {
                margin-top: 16px;
                font-size: 18px;
            }
            .para {
                font-size: 14px;
                line-height: 20px;
            }
        }
    }
    .projects_container {
        margin-top: 42px;
    }
    .sub_visual {
        padding-top: 40px;
        .rough_info {
            padding-top: 64px;
            .title {
                font-size: 22px;
            }
            .sort {
                margin-top: 12px;
                .badge {
                    padding: 2px 10px;
                    font-size: 12px;
                }
            }
            .img_container {
                margin-top: 42px;
            }
        }
        .detail_info {
            .info_list {
                .item {
                    padding: 16px 0;
                    .desc {
                        word-break: break-all;
                    }
                }
            }
        }
    }

    .about_main {
        display: block;
        padding: 64px 0;
        margin-bottom: 64px;
        .img_container {
            display: block;
            width: 120px;
        }
        .text_container {
            display: block;
            margin-top: 34px;
            padding-left: 0;
            .title {
                font-size: 22px;
            }
            .para {
                margin-top: 14px;
                font-size: 16px;
            }
        }
    }
    .history {
        display: block;
        & + & {
            margin-top: 60px;
        }
        .subject {
            display: block;
            width: 100%;
            font-size: 18px;
        }
        .list_wrap {
            display: block;
            padding-left: 0;
            .work_list {
                margin-top: 24px;
                .item {
                    .title {
                        font-size: 16px;
                    }
                    .period {
                        font-size: 14px;
                        margin-top: 6px;
                    }
                    .para {
                        font-size: 14px;
                        margin-top: 6px;
                    }
                }
            }
        }
    }
    .coming_soon {
        .para {
            font-size: 22px;
        }
    }
} ;
