.header {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    z-index: 1;
    &.about {
        .container {
            .inner {
                .gnb {
                    ul {
                        li {
                            &.about {
                                a {
                                    font-weight: 700;
                                }
                            }
                        }
                    }
                }
            }
        }
    }
    &.blank {
        .container {
            .inner {
                .gnb {
                    ul {
                        li {
                            &.blank {
                                a {
                                    font-weight: 700;
                                }
                            }
                        }
                    }
                }
            }
        }
    }
    &.active {
        background-color: #fff;
        .container {
            .inner {
                height: 64px;
            }
        }
        border-bottom: 1px solid rgba(0, 27, 55, 0.1);
    }
    .container {
        .inner {
            display: flex;
            justify-content: space-between;
            align-items: center;
            height: 120px;
            transition: 0.34s ease;
            .logo {
                a {
                    display: block;
                    font-size: 14px;
                    font-weight: 500;
                }
            }
            .gnb {
                ul {
                    display: flex;
                    li {
                        margin-right: 4px;
                        &:last-child {
                            margin-right: 0;
                        }
                        a {
                            display: block;
                            padding: 12px 16px;
                            font-size: 16px;
                            font-weight: 400;
                            color: #425968;
                            &:hover {
                                color: #b0b8c1;
                            }
                        }
                    }
                }
            }
        }
    }
}

@media screen and (max-width: 768px) {
    .header {
        &.active {
            .container {
                .inner {
                    height: 46px;
                }
            }
        }
        .container {
            .inner {
                height: 64px;
                .logo {
                    a {
                        font-size: 13px;
                    }
                }
                .gnb {
                    ul {
                        li {
                            a {
                                padding: 8px;
                                font-size: 14px;
                            }
                        }
                    }
                }
            }
        }
    }
} ;
